import React, { Component } from 'react'
import Btn from './common/Button'



export class Calc extends Component {
    state = {
        text: "",
        btns: [
            { value: "C", type:"primary" },
            { value: "+/-", type:"primary" },
            { value: "%", type:"primary" },
            { value: "/", type:"secoundary" },
            { value: 7, type:"num" },
            { value: 8, type:"num" },
            { value: 9, type:"num" },
            { value: "*", type:"secoundary" },
            { value: 4, type:"num" },
            { value: 5, type:"num" },
            { value: 6, type:"num" },
            { value: "-", type:"secoundary" },
            { value: 1, type:"num" },
            { value: 2, type:"num" },
            { value: 3, type:"num" },
            { value: "+", type:"secoundary" },
            { value: 0, type:"num" },
            { value: ".", type:"num" },
            { value: "=", type:"secoundary" },
        ]
    }
    handleClick = (btn) => {
        if (btn === "C"){
            this.setState({text: ""})
        } else if (btn === "+/-"){
            this.setState({text: -this.state.text})
        } else{
            this.setState({text: this.state.text+btn})
        }
        
        console.log(`btn :`, btn)
        console.log(this.state.text)
    }
    render() {
        return (
            <div className="calc">
                <div className="result">
                    <div className="text">{this.state.text}</div>
                </div>
                <div className="btns">
                    {this.state.btns.map(item => <Btn click={this.handleClick} key={item.value} value={item.value} type={item.type} />)}
                </div>
            </div>
        )
    }
}


