import React, {useState, useEffect} from 'react'
import {motion} from "framer-motion"

function Btn({value, type, click}) {
    const [state, setstate] = useState("btn")
    useEffect(() => {
        if(type === "secoundary"){
            setstate("btn secoundary")
        }else if( value === 0){
            setstate("btn zero")
        } else {
            setstate("btn primary")
        }
    }, [])
    return (
        <motion.div whileTap={{scale: .8}} className={state} onClick={()=>click(value)}>
            <div className="in">
                {value}
            </div>
        </motion.div>
    )
}

export default Btn
